* {
    color: #ffffff;
    font-family: 'Arimo', sans-serif;
    background: black;
}
.body{
}
.App {
    text-align: center;

}

.App .item-todo {
    padding: 14px;
    width: 250px;
    margin: 0 auto;
    text-align: left;
    margin-top: 15px;
    background: linear-gradient(
            90deg,
            #841e00 0%,
            #ff653d  100%
    );
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.App .item-todo .item-text {
    display: inline-block;
    cursor: pointer;
}

.App input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #ff653d;
    outline: none;
    background: transparent;

}

.App button {
    padding: 16px 7px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
            90deg,
            #ff653d 0%,
            #841e00 100%
    );
    color: #fff;
    text-transform: capitalize;
}

.App .item-todo .item-delete {
    display: inline-block;
    cursor: pointer;
    float: right;
    font-size: 22px;
    position: relative;
    left: -4px;
    bottom: 5px;
    color: black;
    background:none;
}

.strike {
    text-decoration: line-through;
}